.item-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0 0 1rem;
}

.form-check {
    margin-left: 1rem;
    margin-bottom: 0;
    cursor :pointer;
}
.form-check input,
.form-check label {
    cursor :pointer;
}
.form-check-label {
    padding-left: .3rem;
}


